.GatherArea-container {
  -webkit-app-region: no-drag;
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100vh;
}

.GatherArea-body {
  overflow: scroll;
  position: absolute;
  width: 100%;
  height: 100vh;
}

.GatherArea-region {
  position: absolute;
  overflow: hidden;
}

.GatherArea-region > iframe {
  width: 100%;
  height: 100%;
}

.GatherArea-avatar {
  position: absolute;
}

.GatherArea-avatar-balloon {
  position: absolute;
  left: -10px;
  bottom: 38px;
  color: black;
  background-color: white;
  border: gray solid 1px;
  border-radius: 20px;
  padding: 3px 6px;
  font-size: 0.5rem;
  text-align: center;
  white-space: nowrap;
}

.GatherArea-facelist {
  -webkit-app-region: drag;
  width: 36px;
}

.GatherArea-toolbar {
  -webkit-app-region: drag;
  overflow: scroll;
  position: absolute;
  left: 40px;
  top: 5px;
  display: flex;
}

.GatherArea-region-editor {
  -webkit-app-region: no-drag;
  margin: 5px;
  padding: 10px;
  background: lightyellow;
  border: 3px solid gray;
  border-radius: 8px;
}

.GatherArea-link-opener {
  -webkit-app-region: no-drag;
  margin: 5px;
  padding: 10px;
  background: skyblue;
  border: 3px solid gray;
  border-radius: 8px;
}
